<script>
import { onMounted, reactive, watch } from "vue";
import axios from "axios";
import vueSwal from "vue-swal";
export default {
    props: {
        getDate: {
            type: Function,
            default: () => {}
        }
    },
    setup(props) {
        const registerUrl =
            "https://script.google.com/macros/s/AKfycbzRgKHX0eSD0ZY_dvSxvXnUodWdGw4y_NLbzJvlLOM2bh5wjjIp/exec";
        const registerDataObj = reactive({
            action: "registered",
            time: "2020/11/16 20:00:00",
            id: "",
            pwd: "",
            confirmPwd: "",
            name: "",
            phone: "",
            birthday: "",
            address: "",
            gender: 0,
            where: 'a',
            about: "",
        });

        const verifyPwd = (obj) => {
            if (obj.pwd !== obj.confirmPwd) {
                swal("密碼不相符!", "請重新輸入");
                return false;
            }

            return true;
        };

        const registerSend = (obj) => {
            const isSamePwd = verifyPwd(obj);

            if (!isSamePwd) {
                return;
            }

            registerDataObj.time = props.getDate();

            axios
                .post(registerUrl, JSON.stringify(registerDataObj))
                .then(function(res) {
                    swal(res.data.res_content);
                })
                .catch(function(error) {
                    console.log(error);
                });
        };

        return { registerDataObj, registerSend };
    },
};
</script>

<template>
    <div class="register">
        <div class="container">
            <form class="panel-login">
                <label class="form-item">
                    <p class="form-title">帳號:</p>
                    <input
                        type="text"
                        class="form-control"
                        placeholder="請輸入帳號"
                        name="account"
                        required
                        v-model.lazy.trim="registerDataObj.id"
                    />
                </label>
                <label class="form-item">
                    <p class="form-title">密碼:</p>
                    <input
                        type="password"
                        class="form-control"
                        placeholder="請輸入密碼"
                        name="pwd"
                        required
                        v-model.lazy.trim="registerDataObj.pwd"
                    />
                </label>
                <label class="form-item">
                    <p class="form-title">密碼確認:</p>
                    <input
                        type="password"
                        class="form-control"
                        placeholder="請輸入密碼"
                        name="pwd_confirm"
                        required
                        v-model.lazy.trim="registerDataObj.confirmPwd"
                    />
                </label>
                <label class="form-item">
                    <p class="form-title">姓名:</p>
                    <input
                        type="text"
                        class="form-control"
                        placeholder="請輸入姓名"
                        name="name"
                        required
                        v-model.lazy.trim="registerDataObj.name"
                    />
                </label>
                <label class="form-item">
                    <p class="form-title">電話:</p>
                    <input
                        type="tel"
                        class="form-control"
                        placeholder="請輸入手機"
                        name="phone"
                        v-model.lazy.trim="registerDataObj.phone"
                    />
                </label>
                <label class="form-item">
                    <p class="form-title">生日:</p>
                    <input
                        type="date"
                        class="form-control"
                        name="birth"
                        v-model.lazy="registerDataObj.birthday"
                    />
                </label>
                <label class="form-item">
                    <p class="form-title">地址:</p>
                    <input
                        type="text"
                        class="form-control"
                        placeholder="請輸入地址"
                        name="address"
                        required
                        v-model.lazy.trim="registerDataObj.address"
                    />
                </label>
                <div class="form-item">
                    <p class="form-title">性別:</p>
                    <label>
                        <input
                            type="radio"
                            name="gender"
                            id=""
                            value="0"
                            required
                            v-model.number="registerDataObj.gender"
                        />
                        男
                    </label>
                    <label>
                        <input
                            type="radio"
                            name="gender"
                            id=""
                            value="1"
                            required
                            v-model.number="registerDataObj.gender"
                        />
                        女
                    </label>
                </div>
                <label class="form-item">
                    <p class="form-title">從哪裡得知:</p>
                    <select
                        id=""
                        class="form-control"
                        name="from_where"
                        v-model.number="registerDataObj.where"
                    >
                        <option selected disabled value="a">請選擇</option>
                        <option value="0">Yahoo 關鍵字</option>
                        <option value="1">Google 關鍵字</option>
                        <option value="2">親友推薦</option>
                        <option value="3">其他</option>
                    </select>
                </label>
                <label class="form-item">
                    <p class="form-title">自介:</p>
                    <textarea
                        id=""
                        class="form-control"
                        cols="30"
                        rows="10"
                        name="info"
                        v-model.lazy.trim="registerDataObj.about"
                    ></textarea>
                </label>
                <button
                    class="btn btn-primary w-100"
                    @click.prevent="registerSend(registerDataObj)"
                >
                    送出
                </button>
            </form>
        </div>
    </div>
</template>

<style lang="scss" scoped></style>
