<script>
export default {
    setup() {
        const getCurrentDatetime = () => {
            const date = new Date();

            return `${date.getFullYear()}/${date.getMonth() +
                1}/${date.getDate()} ${date.getHours()}:${date.getMinutes()}:${date.getSeconds()}`;
        };

        return { getCurrentDatetime };
    },
};
</script>

